@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@469&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
background: #f2b0cc; /* Old browsers */ 
background: linear-gradient(to bottom,  #f2b0cc 14%,#eac7d3 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2b0cc', endColorstr='#eac7d3',GradientType=0 ); 
}
ol, ul, dl {
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  margin-top: 5px;
  font-family:Oswald;
}
#root > .container{
  padding:20px ;
}
#root > .container h1{
  text-transform:uppercase;
}
.post-wrapper  h4{
  padding:10px 0;
  text-transform:uppercase;
}
a{color:#222 !important;}
.ul-inline li{display:inline-block;width: 50%;}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #222;
}
.btn-primary, .btn-primary.disabled, .btn-primary:disabled {
  color: #222!important;
  background-color: #ced4da !important;
  border-color: #ced4da !important;
  border-radius:0;
}

.form-control {
  background-color: #dadcef !important;
  border: 1px solid #dadcef !important;
  border-radius: 0 !important;
}
.post-wrapper {
  padding: 20px;
  background: #dadcef ;
  margin: 0 0 20px 0;
}

h4, .h4 {
  font-size: 1rem;
  margin: 10px 0;
}

h1, .h1{
  margin: 10px 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.form-control{margin:15px 0;}
.comments-wrapper{
  margin:55px 0 0 0;
}
.comments-wrapper > h2{
  text-transform:uppercase;
}
.comments-form-wrapper{
  margin:20px 0 0 0;
}

img{max-width:100%;}
.post-wrapper{
  padding:20px;
}

.post-wrapper img{height:200px;}

.comment {
  margin: 20px 0;
  border-bottom: 0.5px solid #d6d2d2;
}

.comment > p:last-child{
  font-size:1.4rem;
}
body{
    background-color: #ced4da !important;
}
.container{background-color:#fff;}
